import React from 'react';

import {
    Audio,
    Download,
    Image,
    Markdown,
    Video
} from '../blocks';

const Lesson = ({ lesson }) => {
    const renderBlock = (block) => {
        switch(block.type) {
            case 'markdown':
                return(<Markdown {...block} />);
            case 'image':
                return(<Image {...block} />);
            case 'missing':
                return(<Missing {...block} />);
            case 'video':
                return(<Video {...block} />);
            case 'audio':
                return(<Audio {...block} />);
            case 'download':
                return(<Download {...block} />);
            case 'assessment':
                return(<Assessment setData={this.setData.bind(this)} {...block} />);
            case 'assessmentResults':
                return(<AssessmentResults {...block} />);
            case 'quiz':
                return(<Quiz {...block} />);
            default:
                console.warn('Unrecognised component: ' + block.type);
        }

        return null;
    }

    const renderBlocks = () => {
        return lesson.blocks.map(function(block) {
            const renderedBlock = renderBlock(block);

            if (renderedBlock !== null) {
                return (
                    <div key={block.bid}>
                        {renderedBlock}
                    </div>
                );
            }
        });
    }

    return (
        <div>
            {renderBlocks()}
        </div>
    );
}

export default Lesson;
